let self;
import Decode from "@/utils/decode.js"
import defaulImg from "@/static/icon/100.png"
// import Decode from "@/utils/decode.js"
import { nowWeather } from "@/api/weather.js"
export default {
	data() {
		return {
			weather: "36.5℃  晴",
			weatherImg: defaulImg,
			currentStep: 0
		};
	},
	created() {
		this.getWeather(),
			this.getStep()
	},
	methods: {
		getWeather() {

			self = this;
			/* 获取当前位置 */
			uni.getLocation({
				type: 'gcj02',
				isHighAccuracy: true,
				success: function(res) {
					nowWeather({ location: `${res.longitude},${res.latitude}`, key: 'e94ce65c8da94aa69947f2dc1d268596' }).then(res => {
						let data = res.HeWeather6[0].now;
						self.weather = data.tmp + "℃  " + data.cond_txt;
						self.weatherImg = `http://qa7dp7qmh.bkt.clouddn.com/${data.cond_code}.png`
					})
				},
				fail(res) {
					console.log(res)
				}
			});

		},
		getStep() {

			const sessionKey = uni.getStorageSync("sessionKey");
			wx.getWeRunData({
				success(res) {

					// let ress = JSON.parse(res)
					console.log(sessionKey)
					// return
					const decode = new Decode("wx6b49ede83038818e", sessionKey);
					const step = decode.decryptData(res.encryptedData, res.iv)

					let arr = step.stepInfoList.slice(-1)
					self.currentStep = arr[0].step
				},
				fail(res) {
					console.log(res)
				}
			})

		}
	}
}
